export const validationPatterns = {
  EMAIL:
    /^[a-z0-9,!#$%&'*+/=?^_`{|}~-]+(\.[a-z0-9,!#$%&'*+/=?^_`{|}~-]+)*@([a-z0-9-]+\.)+[a-z]{2,}$/i,
  NOT_EMPTY: /^(?!\s*$).+/,
};

export const validateEmail = (email: string): boolean => {
  return validationPatterns.EMAIL.test(email);
};

export const validateNotEmpty = (value: string): boolean => {
  return validationPatterns.NOT_EMPTY.test(value);
};

export const htmlPatterns = {
  EMAIL: validationPatterns.EMAIL.source,
  NOT_EMPTY: validationPatterns.NOT_EMPTY.source,
};
